
import { defineComponent, PropType } from "vue";
import HisBasicTable from "./HisBasicTable.vue";
import { modalController } from "@ionic/vue";
import TextSkeloton from "@/components/TextSkeleton.vue"
import {
  IonHeader, 
  IonToolbar, 
  IonContent, 
  IonFooter, 
  IonButton
} from "@ionic/vue"
import { AsyncTableRowHandler } from "./tables/ReportDataTable";
  
export default defineComponent({
  components: { 
    HisBasicTable,
    IonHeader, 
    IonToolbar, 
    IonContent, 
    IonFooter, 
    IonButton,
    TextSkeloton
  },
  props: {
    title: {
      type: String,
      required: true,
    },
    columns: {
      type: Object as PropType<string[]>,
      required: true,
    },
    onRows: {
      type: Function as PropType<AsyncTableRowHandler>,
      required: true,
    },
  },
  data: () => ({
    rows: [] as Array<any>,
    ready: false,
  }),
  watch: {
    onRows: {
      async handler(func: AsyncTableRowHandler) {
        if (func) {
          this.rows = await func();
          this.ready = true;
        }
      },
      immediate: true,
      deep: true,
    },
  },
  methods: {
    async closeModal() {
      await modalController.dismiss({});
    },
  },
});
